<template>
  <div>
    <h1>Wave Buttons</h1>
    <div>
      <button class="qr-button wave-button" ref="button">Wave Effect</button>
      <button @click="downloadImage">Download Image</button>
    </div>
    <canvas ref="canvas" style="display: none;"></canvas>
  </div>
</template>

<script>
export default {
  methods: {
    downloadImage () {
      const button = this.$refs.button
      const canvas = this.$refs.canvas
      const ctx = canvas.getContext('2d')

      // Set canvas dimensions equal to button dimensions
      const buttonRect = button.getBoundingClientRect()
      canvas.width = buttonRect.width
      canvas.height = buttonRect.height

      // Draw button content to canvas
      ctx.fillStyle = getComputedStyle(button).backgroundColor
      ctx.fillRect(0, 0, canvas.width, canvas.height)
      ctx.font = getComputedStyle(button).fontSize + ' ' + getComputedStyle(button).fontFamily
      ctx.fillStyle = getComputedStyle(button).color
      ctx.textAlign = 'center'
      ctx.textBaseline = 'middle'
      ctx.fillText(button.innerText, canvas.width / 2, canvas.height / 2)

      // Convert canvas to image and download
      const dataURL = canvas.toDataURL('image/png')
      const link = document.createElement('a')
      link.download = 'wave_button.png'
      link.href = dataURL
      link.click()
    }
  }
}
</script>

<style>
/* Common button styles */
.qr-button {
  padding: 15px 30px;
  font-size: 16px;
  color: #fff;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-block;
  margin: 10px;
  background: linear-gradient(45deg, #0077ff, #00ccff);
  position: relative;
  overflow: hidden;
}

/* Wave Button */
.wave-button {
  background: linear-gradient(45deg, #0077ff, #00ccff);
}

.wave-button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300%;
  height: 300%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0) 70%);
  transition: transform 0.5s ease, opacity 0.5s ease;
  transform: translate(-50%, -50%) scale(0);
  opacity: 0;
}

.wave-button:hover::before {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
}

/* Extra Wave Effect */
.wave-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: -45%;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0) 70%);
  transition: transform 0.5s ease;
  transform: translateX(-100%) skewX(-15deg);
}

.wave-button:hover::after {
  transform: translateX(200%) skewX(-15deg);
}
</style>
